<template>
	<div>
		<p>{{resultRank}}</p>
	</div>

</template>

<script>
	
	export default {
		
		created:function(){

			var params=this.$route.params;

			this.filterNum=params.filterNum;
			this.seeBeforeYes=params.seeBeforeYes;
			this.seeBeforeNo=params.seeBeforeNo;

		},
		data:function(){
			return {
				filterNum:"",
				seeBeforeYes:[],
				seeBeforeNo:[],
				nickName:"cutemurphy",
				age:""
			}
		},
		computed:{

			resultRank:function(){

				var filterNum=this.filterNum;
				var yesNum=this.seeBeforeYes.length;
				var noNum=this.seeBeforeNo.length;

				var rankObj={
					"A":"你是个大文青噢，崇拜崇拜～想摘下星星给你～",
					"B":"你是个资深文艺青年噢～想摘下星星给你～",
					"C":"你是个普通文艺青年～",
					"D":"你是个伪文艺青年～该补补书籍电影了",
				}

				var score=(yesNum/filterNum).toFixed(1);

				if(score == 1){
					return rankObj["A"]
				}else if(score >= 0.8 && score < 1){
					return rankObj["B"]
				}else if(score >=0.6 && score < 0.8){
					return rankObj["C"]
				}else{
					return rankObj["D"]
				}


			}
		}

	}

</script>

