<template>
	<div>
		<ul class="movie-items">
			<li v-bind:class="{active: movieItem.show == 1 }" :data-index="index" :data-id="movieItem.rank" v-for="movieItem,index in movieFilterItems">
				<div>
					<p>{{movieItem.rank}}</p>
					<p>
						{{movieItem.title}}
					</p>
					<p>
						<span>导演：</span>{{movieItem.director}}
					</p>
					<p>
						<img :src="movieItem.imgSrc" alt="" style="width:200px;">
					</p>
					<p>
						<span v-on:click="seeBefore(1,index,movieItem.rank)">看过</span>
						<span v-on:click="seeBefore(0,index,movieItem.rank)">没看过</span>
					</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	
	import moviesInfo from '../config/moviesInfo';

	export default {

		data:function(){
			return {
				nickname:"cutemurphy",
				jpg:null,
				seeBeforeYes:[],
				seeBeforeNo:[],
				movieFilterItems:null,
				filterNum:10
			}
		},
		methods:{

			seeBefore:function(check,index,rankId){

					var moviesNum=this.movieFilterItems.length;

					if(check == 1){
						this.seeBeforeYes.push(rankId);
					}else{
						this.seeBeforeNo.push(rankId);
					}

					if(index !== moviesNum-1){
						this.movieFilterItems[index].show=0;
						this.movieFilterItems[index+1].show=1;
					}

					if(this.seeBeforeYes.length + this.seeBeforeNo.length === moviesNum){

						this.$router.push({
							name:"result",
							params:{
								filterNum:this.filterNum,
								seeBeforeYes:this.seeBeforeYes,
								seeBeforeNo:this.seeBeforeNo
							}
						});

					}
					

			},
			getArrItem:function(arr, num) {

	          var temp_array = new Array();
	          for (var index in arr) {
	              temp_array.push(arr[index]);
	          }

	          var return_array = new Array();

	          for (var i = 0; i < num; i++) {
	              if (temp_array.length > 0) {
	                  var arrIndex = Math.floor(Math.random() * temp_array.length);
	                  return_array[i] = temp_array[arrIndex];
	                  temp_array.splice(arrIndex, 1);
	              } else {
	                  break;
	              }
	          }
      		  
      		  return return_array;
  			}
		},
		created:function(){
			
			var movieFilterItems=this.getArrItem(moviesInfo,this.filterNum);

			movieFilterItems.forEach(function(item,index){
				item.show=index == 0 ? 1 : 0;
			})

			this.movieFilterItems=movieFilterItems;

		}

	}

</script>

<style scoped>
*{
			list-style: none;
		}

		.movie-items li{
			display: none;
		}

		.movie-items li.active{
			display: block;
		}
</style>